.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  border-bottom: 1px solid #aaa;
  margin: 0 0 10px;
  padding: 0;
}

.react-tabs__tab {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  border-bottom: none;
  padding: 6px 12px;
  list-style: none;
  display: inline-block;
  position: relative;
  bottom: -1px;
}

.react-tabs__tab--selected {
  color: #000;
  background: #fff;
  border-color: #aaa;
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  height: 5px;
  background: #fff;
  position: absolute;
  bottom: -5px;
  left: -4px;
  right: -4px;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

#main {
  grid-template-columns: 1fr min-contents;
  gap: 0;
  display: grid;
}

.EczEditCodeApp_main_pane {
  grid-area: 1 / 1;
  margin-left: 0;
  margin-right: 0;
  padding-left: 3rem;
  padding-right: 2rem;
  overflow: auto;
}

.EczEditCodeAppSidebar {
  width: max(400px, min(550px, 45vw));
  background-color: rgba(255, 0, 0, .2);
  grid-area: 1 / 2;
  overflow: auto;
}

.EczEditCodeAppSidebar.collapsed {
  width: 25px;
}

.EczEditCodeAppSidebar.collapsed .sidebar-contents {
  display: none;
}

.EczEditCodeAppSidebar .help_text {
  padding: 0 1rem 1rem;
  font-size: .9rem;
}

.EczEditCodeAppSidebar .help_text h1 {
  font-size: 1.2rem;
  font-weight: bold;
}

.EczEditCodeAppSidebar .help_text h2 {
  font-size: 1rem;
  font-weight: bold;
}

.EczEditCodeAppSidebar .help_text ul {
  padding-left: 2rem;
}

.EczEditCodeAppSidebar .help_text code {
  background-color: rgba(0, 0, 0, .1);
  padding: 1px 3px;
  display: inline-block;
}

.EczEditCodeAppSidebar .help_text pre > code {
  padding: 5px;
  display: block;
  overflow-x: auto;
}

.EczEditCodeAppSidebar .help_text li {
  margin-top: 1em;
  margin-bottom: 1em;
}

.EczEditCodeApp_bottombar {
  color: #fff;
  background-color: #006400;
  grid-area: 2 / 1 / auto / 3;
  padding: 8px;
  display: flex;
}

.EczEditCodeApp_bottombar a, .EczEditCodeApp_bottombar a:link, .EczEditCodeApp_bottombar a:visited {
  color: #c8dcff;
}

.EczEditCodeApp_bottombar a:hover, .EczEditCodeApp_bottombar a:active {
