}

.EczEditCodeApp_bottombar .spacer {
  flex: auto;
}

.EczEditCodeApp_bottombar button {
  min-height: 30px;
  margin-right: 1rem;
  font-size: 1.1rem;
}

.EczEditCodeApp_bottombar .no-display {
  display: none;
}

.EczEditSchemaField {
  margin: 1rem 0;
}

.EczEditSchemaField .EczEditSchemaField_fieldname {
