}

.EczEditCodeApp_main_pane > .EczEditSchemaField, .EczEditCodeApp_main_pane > .EczEditSchemaField > .EczEditSchemaField {
  margin-left: 0;
}

.EczEditCodeApp_main_pane > .EczEditSchemaField > .EczEditSchemaField > .EczEditSchemaField_field_name {
