.EczEditCodeApp_main_pane > .EczEditSchemaField > .EczEditSchemaField > .EczEditSchemaField_field_description, .EczEditCodeApp_main_pane > .EczEditSchemaField > .EczEditSchemaField > .EczEditSchemaField_field_expected {
